@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);

body {
  margin: 0;
  background: #ffffff;
  color: #000000;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.heading{
  background: #252525;
  color: #ffffff;
  font-family: monospace;
  letter-spacing: 1em;
  padding: 1% 1%;
  display: flex;
  flex-wrap: wrap;
}

.hrLine{
  border: none;
  outline: none;
}

.btns{
  display: flex;
  flex-wrap: wrap;
  text-align: end;
  position: absolute;
  right: 0%;
  padding: 1.5% 1%;
  word-spacing: 1em;
  width: 45%;
  text-anchor: end;
}

.link{
  color: #b6b2b2;
  text-decoration: none;
  margin-left: 3%;
  text-align: right;
}

.link:hover{
  color: #ffffff;
}

.Block{
  margin: 1% 1%;
  display: flex;
  flex-wrap: wrap;
  background: #000000b7;
  color: #ffffff;
}

.image{
  padding: 1% 1%;
  width: 25%;
}

.projecticon{
  width: 100%;
}

.projectshortdetail{
  width: 70%;
  padding: 0 1% 1% 1%;
  margin-top: -1%;
  text-align: justify;
}

.right{
  position: absolute;
  right: 2%;
}

.crossLine{
  text-decoration: line-through;
}

.projectype{
  padding: 1% 1%;
  position: relative;
  top: -88.5%;
  background: #000000b7;
  width: 18%;
  text-align: center;
  border-radius: 0 0 4px 0;
}

.DetailedBlock{
  padding: 1% 1%;
  margin: 1% 1%;
  background: #000000b7;
  color: #ffffff;
}

.projectHeadText{
  font-size: 110%;
  padding: 1% 1%;
  text-align: center;
  font-weight: bold;
}

.i{
  font-style: italic;
}

li{
  margin-bottom: 0.5%;
}

.footer{
  background: #252525;
  padding: 1% 1%;
  color: #b6b2b2;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
}

.contact{
  width: 33%;
  font-family: monospace;
}
.contact:hover{
  color: #ffffff;
  cursor: default;
}
.contactText{
  font-weight: bold;
}

.follow{
  width: 32%;
  font-family: monospace;
}
.follow:hover{
  color: #ffffff;
  cursor: default;
}
.followText{
  font-weight: bold;
}

.address{
  width: 33%;
  font-family: monospace;
}
.address:hover{
  color: #ffffff;
  cursor: default;
}
.addressText{
  font-weight: bold;
}

.usrForContact{
  padding: 1% 1%;
  outline: none;
  border: none;
  border-bottom: #b6b2b2 2px solid;
  background: none;
  color: #ffffff;
  font-size: 110%;
  font-family: monospace;
}

.queryForContact{
  background: none;
  font-family: monospace;
  border: none;
  outline: none;
  border-bottom: #b6b2b2 2px solid;
  color: #ffffff;
  border-inline: #ffffff 1px solid;
  text-align: justify;
  padding: 1% 1%;
  margin: 0 8%;
}

.btnForContact{
  padding: 1% 2%;
  outline: none;
  cursor: pointer;
  font-weight: bold;
}

.social-icon{
  font-size: 105%;
  background: #000000b7;
  padding: 3% 5%;
}

.facebook:hover{
  animation: facebook 1s ease-in-out forwards;
}

@keyframes facebook {
  to{
    background: #1f31d3;
  }
}

.instagram:hover{
  animation: instagram 1s ease-in-out forwards;
}

@keyframes instagram {
  to{
    background: repeating-linear-gradient(#1f31d3, #d31f1f);
  }
}

.youtube:hover{
  animation: youtube 1s ease-in-out forwards;
}

@keyframes youtube {
  to{
    background: #d31f1f;
  }
}